<template>
    <div class="uploader">
        <ol class="fileList">
            <li v-for="file in fileList" :key="file.name">
                <div class="image-wrapper">
                    <template v-if="file.status === 'uploading'">
                        <g-icon class="spin icon-loading" iconname="loading2"></g-icon>
                    </template>
                    <template v-else-if="file.url.indexOf('image') >= 0">
                        <img class="addFile" :src="file.url" :style="{width:width+'px',height: height+'px'}"
                             alt="">
                        <div class="remove-wrapper">
                            <g-icon class="remove-icon" @click="onRemoveFile(file)" iconname="close"></g-icon>
                        </div>
                    </template>
                    <template v-else>
                        <div class="defaultImage"></div>
                    </template>
                </div>
                <!--<span class="file-name" :class="{[file.status]:file.status}">{{file.name}}</span>-->
            </li>
            <li>
                <div @click="onClickUpload">
                    <div class="addFile" :style="{width:width+'px',height: height+'px'}">
                        <div class="row"></div>
                        <div class="col"></div>
                    </div>
                </div>
            </li>
        </ol>


        <div ref="temp" style="width: 0; height: 0; overflow: hidden;"></div>
    </div>
</template>

<script>
    import GIcon from './icon';
    import alloycrop from './AlloyCrop';
    // import imageCompress from '../../plugins/imageCompression';
    import lrz from 'lrz';
    import axios from 'axios';

    export default {
        name: "uploader",
        components: {GIcon,},
        props: {
            imageCut: {
                type: Boolean,
                default: true,
            },
            action: {
                type: String,
                required: true
            },
            name: {
                type: String,
                required: true
            },
            method: {
                type: String,
                default: 'POST'
            },
            parseResponse: { //,url
                type: Function,
                required: true
            },
            fileList: {  //
                type: Array,
                default: () => []
            },
            height: {
                type: [Number, String],
                default: 80
            },
            width: {
                type: [Number, String],
                default: 80
            }
        },
        methods: {
            //,4
            // calculateWidth() {
            //     let width = document.documentElement.clientWidth;
            //     return ((width - 64) / 4) + 'px';
            // },
            onClickUpload() { //
                let input = this.createInput();
                input.addEventListener('change', () => {
                    if (this.imageCut) {
                        this.choosePhoto(input.files[0]);
                    } else {
                        this.uploadFiles(undefined, input.files);
                    }
                    //this.uploadFiles(input.files[0]); //
                    // this.uploadFiles(input.files); //
                    input.remove(); //input
                });
                input.click();
            },
            createInput() { // input
                this.$refs.temp.innerHTML = '';
                let input = document.createElement('input');
                input.type = 'file';
                input.multiple = !this.imageCut; //
                this.$refs.temp.appendChild(input);
                return input;
            },
            beforeUploadFile(rawFile, newName) { //
                //
                let {size, type} = rawFile;
                this.$emit('addFile', {name: newName, size, type, status: 'uploading'});
                //this.$emit('update:fileList', [...this.fileList, {name: newName, size, type, status: 'uploading'}]);
                //fileList uploading()
                return true;
            },
            choosePhoto(file) {
                // 
                const reader = new FileReader();
                // fileReader 
                if (typeof FileReader === 'undefined') {
                    alert('');
                } else {
                    // 
                    reader.readAsDataURL(file); // base64
                    reader.onload = () => {
                        this.clipImg(reader.result, file); // 
                    };
                }
            },
            clipImg(src, rawFiles) {
                new alloycrop({
                    image_src: src,
                    circle: false,
                    width: 300,
                    height: 300,
                    output: 1,
                    ok: (base64, canvas) => {
                        console.log('ok');
                        this.uploadFiles(base64, rawFiles);
                    },
                    cancel: () => { },
                    ok_text: '',
                    cancel_text: '',
                });
            },
            uploadFiles(cutFile, rawFile) { //
                console.log(rawFile);
                if (!this.imageCut) {
                    Array.from(rawFile).map(file => {
                        let {name} = file; // 
                        let newName = this.generateName(name); //
                        this.beforeUploadFile(rawFile, newName); //
                        lrz(file, {
                            width: 800,
                            quality: 0.5,
                        }).then(res => {
                            console.log(res);
                            let base64File = {
                                filename: newName,
                                file: res.base64,
                                time: new Date()
                            };
                            this.ajax(base64File, newName);
                        });

                    });
                } else {
                    let {name} = rawFile; // 
                    let newName = this.generateName(name); //
                    this.beforeUploadFile(rawFile, newName); //
                    let base64File = {
                        filename: newName,
                        file: cutFile,
                        time: new Date()
                    };
                    // let formData = new FormData(); //
                    // rawFile.base64 = cutFile;
                    // formData.append(this.name, rawFile); // formData  append  https://developer.mozilla.org/zh-CN/docs/Web/API/FormData/append
                    // console.log(formData);
                    // this.ajax(base64File, (response) => { //
                    //     console.log('responese', response);
                    //     let url = this.parseResponse(response); // ,(ID,  ,)
                    //     this.afterUploadFiles(url, newName); //
                    // }, (xhr) => {
                    //     console.log('xhr', xhr);
                    //     this.uploadError(newName); //
                    // });
                    this.ajax(base64File, newName);
                }
            },
            afterUploadFiles(url, newName) {
                console.log(this.fileList);
                let file = this.fileList.filter(item => item.name === newName)[0]; //
                let index = this.fileList.indexOf(file); //  fileList 
                let copyFile = JSON.parse(JSON.stringify(file)); //
                copyFile.url = url;
                copyFile.status = 'success'; // success ()
                let fileListCopy = [...this.fileList]; // fileList
                fileListCopy.splice(index, 1, copyFile); //   fileList
                console.log('fileListCopy');
                console.log(fileListCopy);
                this.$emit('update:fileList', fileListCopy); //
            },
            generateName(name) {
                //name
                while (this.fileList.filter((file) => file.name === name).length > 0) {
                    let dotIndex = name.lastIndexOf('.');
                    let nameWithOutExtension = name.substring(0, dotIndex);
                    let extension = name.substring(dotIndex);
                    name = nameWithOutExtension + '(1)' + extension;
                }
                return name;
            },
            ajax(formData, newName) {
                axios.post(this.action, {
                    ...formData
                }).then(r => {
                    let url = this.parseResponse(r.data.file); // ,(ID,  ,)
                    this.afterUploadFiles(url, newName); //
                }).catch(() => {
                    this.uploadError(newName);
                });
            },
            uploadError(newName) { //
                let errorFile = this.fileList.filter(f => f.name === newName)[0];
                let errorFileCopy = JSON.parse(JSON.stringify(errorFile));
                errorFileCopy.status = 'fail';
                let fileList = [...this.fileList];
                let index = fileList.indexOf(errorFile);
                fileList.splice(index, 1, errorFileCopy);
                this.$emit('update:fileList', fileList);
            },
            onRemoveFile(file) { //
                let copy = JSON.parse(JSON.stringify(this.fileList));
                copy.filter((item, index) => {
                    if (item.name === file.name) {
                        copy.splice(index, 1);
                    }
                });
                this.$emit('update:fileList', copy);
            }

        }
    };
</script>

<style lang="less" scoped>

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    .spin {
        animation: spin 2s infinite linear;
    }

    .uploader {
        .addFile {
            /*height: 80px;*/
            /*width: 80px;*/
            border: 1px solid #ddd;
            position: relative;
            margin: 8px 4px;
            .row {
                width: 60%;
                height: 0;
                border: 1px solid #ddd;
                position: absolute;
                top: 50%;
                left: 20%;
            }
            .col {
                width: 0;
                height: 60%;
                border: 1px solid #ddd;
                position: absolute;
                left: 50%;
                top: 20%;
            }
        }
        .fileList {
            list-style: none;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            margin-left: 12px;
            .image-wrapper {
                height: 80px;
                width: 80px;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                .icon-loading {
                    height: 50%;
                    width: 50%;
                }
                .remove-wrapper {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: flex;
                    justify-content: flex-end;
                }
            }
            li {
                display: flex;
                align-items: center;
                margin: 8px 4px;
                /*padding: 5px;*/
                .defaultImage {
                    height: 80px;
                    width: 80px;
                    background-color: #ddd;
                }
            }
            .file-name {
                margin-right: auto;
            }
            .success {
                color: green;
            }
            .fail {
                color: red;
            }
            .upload {
                color: #ddd;
            }

        }
    }


</style>